html, body, div, h1, h2, h3, p, span, ul, li {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, system-ui, sans-serif;
}

.catalog {
  grid-gap: 48px 46px;
  grid-template-columns: 1fr;
  justify-items: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 1fr);
  }
}

.catalog-card {
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: color .3s;
}

.catalog-card_title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  transition: color .3s;
}

.catalog-card:hover .catalog-card_title {
  color: #34568b;
}

.catalog-card_image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.catalog-card_subtitle {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.catalog-card_button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s, color .3s;
}

.catalog-card_button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.catalog-price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.catalog-price_label {
  color: #616070;
  font-size: 12px;
}

.catalog-price_value {
  font-size: 16px;
  font-weight: 700;
}

.card-stars_rating {
  font-size: 10px;
  line-height: 14px;
}

.stars {
  list-style: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars-wrapper {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  border: 1px solid #f3f3f3;
  justify-content: space-between;
  align-items: center;
  padding: 10px 50px;
  display: flex;
}

.header_link_logo {
  width: 40px;
  height: 40px;
}

.nav-list {
  list-style: none;
  display: flex;
}

.nav-list_item:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.nav_link:hover {
  color: #00acdc;
}

.nav_link--active {
  color: #00acdc;
  position: relative;
}

.nav_link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: background-color .3s, width .3s;
  display: block;
  position: absolute;
  bottom: -19px;
  left: 0;
  right: 0;
}
